<template>
  <section class="modlude">
    <div class="iot_smart_box" v-for="item in configInfor" :key="item.id">
      <h3 class="main_name">{{item.main.mainName}}</h3>
      <el-row class="iot_smart_btn">
        <!-- <el-button type="primary"></el-button> -->
        <el-button type="warning" @click="pushpushThisConfig(item.id)"></el-button>
        <el-button type="danger" @click="deletedelProjectConfig(item.id)"></el-button>
      </el-row>
      <!--  -->
      <div class="iot_smart_module">
        <div>
          <el-button type="primary" size="small" style="float:right;margin-left:20px;" @click="iotglobalparamDialog=true,nowConfigInforId=item.id"></el-button>
        </div>
        <div class="module_cont" v-if="item.globalParam">
          <div class="module_cont_json">
            <el-row :gutter="20">
              <el-col :span="4"><span></span><span  class="color_active">{{item.globalParam.workPattern==0?"":item.globalParam.workPattern==1?"":""}}</span></el-col>
              <el-col :span="4"><span></span><span class="color_active">{{item.globalParam.protocolVersion}}</span></el-col>
            </el-row>
          </div>
        </div>
        <div style="color:#999;" v-else></div>
      </div>
      <div class="iot_smart_module">
        <div>
          <el-button type="primary" size="small" style="float:right;margin-left:20px;" @click="iotprojectparamDialog=true,nowConfigInforId=item.id"></el-button>
        </div>
        <div class="module_cont" v-if="item.projectParam">
          <div class="module_cont_json">
            <el-row :gutter="20">
              <el-col :span="4"><span></span><span  class="color_active">{{item.projectParam.describer}}</span></el-col>
              <el-col :span="4"><span></span><span class="color_active">{{item.projectParam.groupRemark}}</span></el-col>
              <el-col :span="4"><span></span><span class="color_active">{{item.projectParam.userName}}</span></el-col>
              <el-col :span="4"><span></span><span class="color_active">{{item.projectParam.userPass}}</span></el-col>
              <el-col :span="4"><span></span><span class="color_active">{{item.projectParam.serverHost}}</span></el-col>
              <el-col :span="4"><span></span><span class="color_active">{{item.projectParam.serverPort}}</span></el-col>
            </el-row>
            <el-row :gutter="20">
              <el-col :span="4"><span>ID</span><span  class="color_active">{{item.projectParam.subnetId}}</span></el-col>
              <el-col :span="4"><span>ID</span><span class="color_active">{{item.projectParam.devId}}</span></el-col>
            </el-row>
          </div>
        </div>
        <div style="color:#999;" v-else></div>
      </div>
      <div class="iot_smart_module">
        <div> 
          <el-button type="danger" size="small" style="float:right;margin-left:20px;" @click="deleInfor"></el-button>
          <el-button type="primary" size="small" style="float:right;" @click="showAddUsers(item.id,item.xqId,item.main.mainType,item.main.mainId),addDialog=true"></el-button>
        </div>
        <div class="module_cont" v-if="item.users">
          <el-table :data="item.users" style="width: 100%" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="nickname" label=""></el-table-column>
            <el-table-column prop="phone" label=""></el-table-column>
            <el-table-column prop="showName" label=""></el-table-column>
          </el-table>
        </div>
        <div style="color:#999;" v-else></div>
      </div>
      <!--  -->
      <div class="iot_smart_module">
        <div>  
          <el-select v-model="picInfor.tempImgMode" placeholder="" size="small">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="0"></el-option>
          </el-select>
        </div>
        <div class="module_cont" v-if="item.screens">
            <div class="module_cont_screens">
              <div class="module_cont_screens_item" v-for="scItem in item.screens" :key="scItem.id">
                <div>
                  <img :src="scItem.img" alt="" v-if="picInfor.tempImgMode==1" @click="gettoShowImgs(scItem.id,scItem.xqId,scItem.typeCode),picsDialog=true">
                  <img :src="scItem.imgoff" alt="" v-if="picInfor.tempImgMode==0" @click="gettoShowImgs(scItem.id,scItem.xqId,scItem.typeCode),picsDialog=true">
                </div>
                <div style="margin-top:10px;">
                  <el-input size="small" v-model="scItem.name" placeholder="" :value="scItem.name"></el-input>
                </div>
                <div class="module_cont_screens_bot">
                  <el-button v-if="item.isHdlMqtt==1" size="mini" @click="getmqtt(scItem,1)">mqtt</el-button>
                  <el-button type="primary" size="mini" @click="changesetNameToEntity(scItem)"></el-button>
                  <el-button type="danger" size="mini" @click="deletedelDeviceOne(scItem.id,scItem.typeCode)"></el-button>
                </div>
              </div>
            </div>
        </div>
        <div style="color:#999;" v-else></div>
      </div>
      <!--  -->
      <div class="iot_smart_module">
        <div>Room  
          <!-- <el-button type="warning" size="small"></el-button> -->
        </div>
        <div class="module_cont" v-if="item.zones">
          <div class="module_cont_room">
            <div class="room_name" v-for="zmItem in item.zones" :key="zmItem.id">
              <h4 style="margin-bottom:20px;text-align:center;">{{zmItem.name}}</h4>
            
              <div class="" v-for="zmmoItem in zmItem.modules" :key="zmmoItem.id">
                <div class="iot_smart_module">
                  <div>{{zmmoItem.name}}  
                    <!-- <el-button type="warning" size="small"></el-button> -->
                    <el-button type="danger" size="small" style="float:right;" @click="deletedelModule(zmmoItem.roomId,zmmoItem.typeCode)"></el-button>
                  </div>
                  <div class="module_cont" v-if="zmmoItem.devices">
                      <div class="module_cont_screens">
                        <div class="module_cont_screens_item" v-for="zmmoItemItem in zmmoItem.devices" :key="zmmoItemItem.id">
                          <div>
                            <img :src="zmmoItemItem.img" alt="" @click="gettoShowImgs(zmmoItemItem.id,zmmoItem.xqId,zmmoItem.typeCode),picsDialog=true,picInfor.tempImgMode=1">
                          </div>
                          <div style="margin-top:10px;">
                            <el-input size="small" v-model="zmmoItemItem.name" placeholder="" :value="zmmoItemItem.name"></el-input>
                          </div>
                          <div class="module_cont_screens_bot">
                            <el-button v-if="item.isHdlMqtt==1" size="mini" @click="getmqtt(zmmoItemItem,2)">mqtt</el-button>
                            <el-button type="primary" size="mini" @click="changesetNameToEntity(zmmoItemItem,zmmoItem.typeCode)"></el-button>
                            <el-button type="danger" size="mini" @click="deletedelDeviceOne(zmmoItemItem.id,zmmoItem.typeCode)"></el-button>
                          </div>
                        </div>
                      </div>
                  </div>
                  <div style="color:#999;" v-else></div>
                </div>
              </div>
            </div>
            
          </div>
        </div>
        <div style="color:#999;" v-else></div>
      </div>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialog">
      <el-table :data="formData" style="width: 100%" stripe @selection-change="handleSelectionChange1">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="nickname" label=""></el-table-column>
        <el-table-column prop="phone" label=""></el-table-column>
        <el-table-column prop="sectionName" label=""></el-table-column>
        <el-table-column prop="houseName" label=""></el-table-column>
      </el-table>
      <paging @changePage = handleCurrentPage :get-total='total'></paging>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="addDialog = false"> </el-button>
        <el-button size="medium " @click="addbindUserForConfig"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="picsDialog">
      <el-form class="form_inline">
        <el-form-item label="" size="small">
          <el-input size="small" v-model="picInfor.imgTypeName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="small" @click="showImport=false"></el-button>
          <el-button size="small" @click="addEntityImg"></el-button>
        </el-form-item>
      </el-form>
      <div v-for="(item,index) in picsArr" :key="index">
        <div class="picPot"><span class="" style="color:#a1d6f4;">{{item.imgTypeName}}</span> <el-button type="danger" size="small" @click="iotdelAllImg(item.id)"></el-button></div>
        <div>
          <!-- <el-upload
            class="avatar-uploader"
            action="/intellmanagerV3.0/iot/uploadImgFile"
            :data="uploadImg"
            :headers="headers"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            >
            <img v-if="imageUrl&&uploadImg.entityImgId==item.id" :src="imageUrl" class="avatar" @click="getImgid(item.id)">
            <i v-else class="el-icon-plus avatar-uploader-icon" @click="getImgid(item.id)"></i>
          </el-upload> -->
        <el-upload
          action="/intellmanagerV3.0/iot/uploadImgFile"
          :data="uploadImg"
          :headers="headers"
          list-type="picture-card"
          :file-list="item.imgNames"
          :on-success="handleAvatarSuccess">
          <i slot="default" class="el-icon-plus" @click="getImgid(item.id)"></i>
          <div slot="file" slot-scope="{file}">
            <img
              class="el-upload-list__item-thumbnail"
              :src="file.url" alt=""
            >
            <span class="el-upload-list__item-actions">
              <!-- <span
                class="el-upload-list__item-preview"
              >
                <i class="el-icon-zoom-in"></i>
              </span> -->
              <span
                v-if="!disabled"
                class="el-upload-list__item-delete"
                @click="setImgeToEntity(file)"
              >
                <i class="el-icon-download"></i>
              </span>
              <span
                v-if="!disabled"
                class="el-upload-list__item-delete"
                @click="iotdelImg(file,item.id)"
              >
                <i class="el-icon-delete"></i>
              </span>
            </span>
          </div>
        </el-upload>
        </div>
      </div>
    </el-dialog>
    <!-- hdl mqtt -->
    <el-dialog title="mqtt" :visible.sync="mqttDialog">
      <iotsmarthomemqtt :mqttInfor="mqttInfor" :typeObj="typeObj"></iotsmarthomemqtt>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="iotglobalparamDialog">
      <iotglobalparam :iotglobalparamtype="iotglobalparamtype" @getGobalInfor="getGobalInfor"></iotglobalparam>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="iotprojectparamDialog">
      <iotprojectparam :iotProjectparamtype="iotProjectparamtype" @getProjectInfor="getProjectInfor"></iotprojectparam>
    </el-dialog>
  </section>
</template>

<script>
import paging from "../paging"
import iotsmarthomemqtt from "./iot_smart_home_mqtt"
import iotglobalparam from "./iot_global_param"
import iotprojectparam from "./iot_project_param"
import {bindGlobalParamForConfig,bindProjectParamForConfig,setImgeToEntity,iotdelImg,iotdelAllImg,addEntityImg,toShowImgs,deleteunBindUserForConfig,toSelectUsers,bindUserForConfig,delProjectConfig,pushThisConfig,setNameToEntity,getIotDetailData,delDeviceOne,delModule} from "../../url/api"
export default {
  data(){
    return{
      headers:{
        Authorization:sessionStorage.getItem('Authorization'),
        token:sessionStorage.getItem('token')
      },
      disabled:false,
      iotglobalparamDialog:false,
      iotprojectparamDialog:false,
      screensType:1,
      iotglobalparamtype:1,//
      iotProjectparamtype:1,//
      isHdlMqtt:null,
      devicesType:1,
      projectConfigId:'',
      uploadToRealPath:'',
      picsDialog:false,
      mqttDialog:false,
      mqttInfor:{},
      xqId:'',
      total:0,
      addDialog:false,
      dataInfor:JSON.parse(this.$route.query.configInfor),
      configInfor:[],
      formData:[],
      deleBatch:[],
      addBatch:[],
      seach:{
        current:1,
        size:10,
      },
      picSeach:{

      },
      nowConfigInforId:'',
      typeObj:1,//1changjin,2fangjian
      picsArr:[],
      imageUrl:'',
      picInfor:{
        tempImgMode:1
      },
      uploadImg:{//
        entityImgId:''
      }
    }
  },
  methods:{
    getGobalInfor(val){//
      this.iotglobalparamDialog=false
      let params = {
        projectId:this.nowConfigInforId,
        globalParamId:val.id,
      }
      bindGlobalParamForConfig(params).then(res=>{
        console.log(res)
        if(res.data.code == 200){
          this.$message("")
          this.getlist()
        }else{
          this.$message("")
        }
      })
    },
    getProjectInfor(val){//
      
      this.iotprojectparamDialog = false
      let params = {
        projectId:this.nowConfigInforId,
        projectParamId:val.id,
      }
      bindProjectParamForConfig(params).then(res=>{
        console.log(res)
        if(res.data.code == 200){
          this.$message("")
          this.getlist()
        }else{
          this.$message("")
        }
      })
    },
    getmqtt(val,type){//mqtt
      console.log(val,type)
      this.typeObj = type
      this.mqttDialog = true
      this.mqttInfor = val
    },
    getImgid(id){
      console.log(id)
      this.uploadImg.entityImgId=id
    },
    setImgeToEntity(file){//
      console.log(file)
      let params = {
        tempId:this.picInfor.tempId,
        tempImgMode:this.picInfor.tempImgMode,
        tempImgName:file.urll,
        tempTypeCode:this.picInfor.typeCode,
      }
      console.log(params)
      setImgeToEntity(params).then(res=>{
        console.log(res)
        if(res.data.code == 200){
          this.$message("")
          this.getlist()
        }
        this.picsDialog = false
      })
    },
    iotdelImg(file,id){//

      let params={
        entityImgId:id,
        imgName:file.urll
      }
      this.$confirm('')
      .then(_ => {
        iotdelImg(params).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.gettoShowImgs(this.picSeach.id,this.picSeach.xqId)
          }
        })
      })
      .catch(_ => {});
    },
    iotdelAllImg(id){//
      let params={
        entityImgId:id
      }
      // this.$confirm('?', '', {
      //   confirmButtonText: '',
      //   cancelButtonText: '',
      //   type: 'warning'
      //   }).then(() => {
      //   iotdelAllImg(parmas).then((res)=>{//
      //     console.log(res)
      //     if(res.data.code == 200){
      //       this.gettoShowImgs(this.picSeach.id,this.picSeach.xqId)
      //       this.$message("")
      //     }else{
      //       this.$message("")
      //     }
      //   })
      //   }).catch(() => {});
      this.$confirm('')
      .then(_ => {
        iotdelAllImg(params).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.gettoShowImgs(this.picSeach.id,this.picSeach.xqId)
          }
        })
      })
      .catch(_ => {});
    },
    addEntityImg(){//
      let parmas = this.picInfor
      console.log(parmas)
      addEntityImg(parmas).then(res=>{
        console.log(res)
        if(res.data.code == 200){
          this.gettoShowImgs(this.picSeach.id,this.picSeach.xqId)
        }
      })
    },
    gettoShowImgs(id,xqId,typeCode){//
      this.picInfor.xqId=xqId
      this.picInfor.tempId=id
      this.picInfor.typeCode=typeCode
      let params = {
        xqId:xqId,
      }
      console.log(params)
      toShowImgs(params).then(res=>{
        console.log(res)
        if(res.data.code == 200){
          // this.picsArr = res.data.data
          this.picsArr = res.data.data.filter(item=>{
            if(item.imgNames){
              item.imgNames.filter(itemm=>{
                itemm.urll= itemm.url
                itemm.url = item.url+itemm.url
                return itemm
              })
            }
            return item
          })
        }
      })
    },
    showAddUsers(id,xqId,mainType,mainId){
      console.log(id)
      this.projectConfigId = id
      this.xqId = xqId
      this.seach.mainType = mainType
      this.seach.mainId = mainId
      toSelectUsers(this.seach).then(res=>{//
        console.log(res)
        if(res.data.code == 200){
          this.formData=res.data.data.records
          this.total = res.data.data.total
        }
      })
    },
    deletedelDeviceOne(id,typeCode){//
      // this.$confirm('')
      // .then(_ => {
        let parmas={
          id:id,
          typeCode:typeCode,
        }
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
          }).then(() => {
          delDeviceOne(parmas).then((res)=>{//
            console.log(res)
            if(res.data.code == 200){
              this.getlist()
              this.$message("")
            }else{
              this.$message("")
            }
          })
          }).catch(() => {});

    },
    deletedelModule(id,typeCode){//module
      let parmas={
        roomId:id,
        typeCode:typeCode,
      }
        this.$confirm('?', '', {
          confirmButtonText: '', 
          cancelButtonText: '',
          type: 'warning'
          }).then(() => {
          delModule(parmas).then((res)=>{//
            console.log(res)
            if(res.data.code == 200){
              this.getlist()
              this.$message("")
            }else{
              this.$message("")
            }
          })
          }).catch(() => {});
    },
    getlist(){
      let params = this.dataInfor
      getIotDetailData(params).then((res)=>{//.
        console.log(res)
        if(res.data.code == 200){
          this.configInfor = res.data.data
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    changesetNameToEntity(obj,typeCode){//
      console.log(obj)
      let params = {
        tempId:obj.id,
        tempImgName:obj.name,
        tempTypeCode:typeCode||obj.typeCode,
      }
      setNameToEntity(params).then(res=>{
        console.log(res)
        if(res.data.code == 200){
          this.getlist()
          this.$message("")
        }else{
          this.$message("")
        }
      })
    },
    deletedelProjectConfig(id){//
      let params = {
        projectConfigId:id
      }
      delProjectConfig(params).then(res=>{
        console.log(res)
        if(res.data.code == 200){
          this.$message("")
          this.$router.go(-1)
        }else{
          this.$message("")
        }
      })
    },
    pushpushThisConfig(id){//
    console.log(id)
      let params = {
        configId:id
      }
      pushThisConfig(params).then(res=>{
        console.log(res)
        if(res.data.code == 200){
          this.$message("")
        }else{
          this.$message("")
        }
      })
    },
    handleCurrentPage(val){//
      this.seach.current=val
      this.showAddUsers()
    },
    handleSelectionChange(val,self) {//
      console.log(val)
      this.deleBatch = val
    },
    handleSelectionChange1(val,self) {//
      console.log(val,self)
      this.addBatch = val
    },
    addbindUserForConfig(){//
      let params = {
        acHouseUserVoList:[],
        projectConfigId:this.projectConfigId,
        xqId:this.xqId
      }
      params.acHouseUserVoList = this.addBatch
      bindUserForConfig(params).then(res=>{
        console.log(res)
        if(res.data.code == 200){
          this.$message("")
          this.getlist()
        }
      })
      this.addDialog = false
    },
    deleInfor(){//
      let arrId = ''
      if(this.deleBatch.length!=0){
        this.deleBatch.forEach((item)=>{
          arrId = item.id+"_"+arrId
        })
      }
      console.log(arrId)
      this.$confirm('')
      .then(_ => {
        deleteunBindUserForConfig(arrId).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getlist()
          }
        })
      })
      .catch(_ => {});
    },
    handleAvatarSuccess(res, file) {
      console.log(res)
      this.imageUrl = URL.createObjectURL(file.raw);
      // this.addEntityImg(res.data[0])
      this.gettoShowImgs(this.picSeach.id,this.picSeach.xqId)
    },
  },
  mounted(){
    this.uploadToRealPath = this.$root.uploadToRealPath
    // console.log(this.$root.uploadToRealPath)
    this.getlist()
    // this.showAddUsers()
    console.log(JSON.parse(this.$route.query.configInfor) )
  },
  components:{
    paging,
    iotsmarthomemqtt,
    iotprojectparam,
    iotglobalparam,
  }
}
</script>

<style scoped>
.iot_smart_box{
  margin-top:10px;
}
.main_name{
  text-align: center;
  margin-bottom:20px;
}
.iot_smart_btn{
  margin-bottom:20px;
}
.iot_smart_module{
  margin-bottom:20px;
}
.module_cont{
  margin-top:10px;
}
.color_active{
  color:#19dcf3;
}
.module_cont_json{
  background: rgba(12, 41, 66, 0.8);
  padding:20px 10px;
}
.module_cont_screens_item{
  display: inline-block;
  width:220px;
  margin-right:20px;
  margin-bottom:20px;
}
.module_cont_screens_item img{
  display: block;
  width:50px;
  height:50px;
  margin:0 auto;
}
.module_cont_screens_bot{
  display: flex;
  justify-content: space-around;
  margin-top:10px;
}
.picPot{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom:10px;
}
</style>


