<template>  
  <section class="modlude">
    <el-form :inline="true" :model="formSearch" class="form_inline">
      <el-form-item label="" size="small">
        <el-cascader
            ref="cascader"
            v-model="option1"
            :options="dataTree"
            :props="defaultProps2"
            @change="handleChange"
            clearable
          >
          </el-cascader>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.xqId" placeholder="">
          <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.groupRemark"></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.describer"></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.userName"></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.userPass"></el-input>
      </el-form-item>
      <el-form-item label="ip" size="small">
        <el-input v-model="formSearch.serverHost"></el-input>
      </el-form-item>
      <el-form-item label="port" size="small">
        <el-input v-model="formSearch.serverPort"></el-input>
      </el-form-item>
      <el-form-item label="ip" size="small">
        <el-input v-model="formSearch.devHost"></el-input>
      </el-form-item>
      <el-form-item label="port" size="small">
        <el-input v-model="formSearch.devPort"></el-input>
      </el-form-item>
      <el-form-item label="ID" size="small">
        <el-input v-model="formSearch.subnetId"></el-input>
      </el-form-item> 
      <el-form-item label="ID" size="small">
        <el-input v-model="formSearch.devId"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="medium " @click="getlist"> </el-button>
        <el-button size="medium " @click="addDialog = true,imageUrl = ''"> </el-button>
      </el-form-item>
    </el-form>
    <el-table :data="formData" style="width: 100%" stripe>
      <el-table-column label="" v-if="iotProjectparamtype==1">
        <template slot-scope="scope">
          <el-radio v-model="iotProjectRadio" :label="scope.row">{{scope.row.describer}}</el-radio>
        </template>
      </el-table-column>
      <el-table-column prop="describer" label="" v-else></el-table-column>
      <el-table-column prop="devHost" label="ip"></el-table-column>
      <el-table-column prop="devId" label="ID"></el-table-column>
      <el-table-column prop="devPort" label="port"></el-table-column>
      <el-table-column prop="groupRemark" label=""></el-table-column>
      <el-table-column prop="hardwareDeviceName" label=""></el-table-column>
      <el-table-column prop="serverHost" label="ip"></el-table-column>
      <el-table-column prop="serverPort" label="port"></el-table-column>
      <el-table-column prop="subnetId" label="ID"></el-table-column>
      <el-table-column prop="userName" label=""></el-table-column>
      <el-table-column prop="userPass" label=""></el-table-column>
      <el-table-column prop="xqName" label=""></el-table-column>
      <el-table-column label="" fixed="right" width=250 v-if="iotProjectparamtype!=1">
				<template slot-scope="scope">
					<el-button type="warning" size="small" @click="updateShowBox(scope.row)"> </el-button>
					<el-button type="danger" size="small" @click="deleInfor(scope.row.id)"> </el-button>
				</template>
			</el-table-column>
    </el-table>
    <paging @changePage = handleCurrentPage :get-total='total'></paging>
    <div v-if="iotProjectparamtype==1">
			<el-button type="warning" size="small" @click="goiotsmarthome1"> </el-button>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="80px" :model="formPush" ref='addList'> 
          <el-form-item label="" size="small" prop="xqId">
            <el-select v-model="formPush.xqId" placeholder="">
              <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-table :data="formData1" style="width: 100%">
              <el-table-column width="25">
                <template slot-scope="scope">
                  <el-radio v-model="formPush.hardwareDeviceId" :label="scope.row.id" style="color:transparent;"></el-radio>
                </template>
              </el-table-column>
              <el-table-column prop="name" label=""></el-table-column>
              <el-table-column prop="xqName" label=""></el-table-column>
              <el-table-column prop="makeCompany" label=""></el-table-column>
              <el-table-column prop="busInter" label="BUS"></el-table-column>
              <el-table-column prop="code" label=""></el-table-column>
            </el-table>
            <div class="pagination">
              <el-pagination
                layout="prev, pager, next"
                prev-text=""
                next-text=""
                @current-change="handleCurrentPage1"
                :page-size="10" 
                :total="total1">
              </el-pagination>
            </div>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formPush.groupRemark"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formPush.describer" type="textarea" :autosize="{ minRows: 2, maxRows: 4}"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formPush.userName"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formPush.userPass"></el-input>
          </el-form-item>
          <el-form-item label="ip" size="small">
            <el-input v-model="formPush.serverHost"></el-input>
          </el-form-item>
          <el-form-item label="port" size="small">
            <el-input v-model="formPush.serverPort"></el-input>
          </el-form-item>
          <el-form-item label="ip" size="small">
            <el-input v-model="formPush.devHost"></el-input>
          </el-form-item>
          <el-form-item label="port" size="small">
            <el-input v-model="formPush.devPort"></el-input>
          </el-form-item>
          <el-form-item label="ID" size="small">
            <el-input v-model="formPush.subnetId"></el-input>
          </el-form-item> 
          <el-form-item label="ID" size="small">
            <el-input v-model="formPush.devId"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="addDialog = false"> </el-button>
        <el-button size="medium " @click="addList('addList')"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="updateDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="80px" :model="formUpdate" ref='formUpdate'> 
          <el-form-item label="" size="small" prop="xqId">
            <el-select v-model="formUpdate.xqId" placeholder="">
              <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-table :data="formData1" style="width: 100%">
              <el-table-column width="25">
                <template slot-scope="scope">
                  <el-radio v-model="formUpdate.hardwareDeviceId" :label="scope.row.id" style="color:transparent;"></el-radio>
                </template>
              </el-table-column>
              <el-table-column prop="name" label=""></el-table-column>
              <el-table-column prop="xqName" label=""></el-table-column>
              <el-table-column prop="makeCompany" label=""></el-table-column>
              <el-table-column prop="busInter" label="BUS"></el-table-column>
              <el-table-column prop="code" label=""></el-table-column>
            </el-table>
            <div class="pagination">
              <el-pagination
                layout="prev, pager, next"
                prev-text=""
                next-text=""
                @current-change="handleCurrentPage1"
                :page-size="10" 
                :total="total1">
              </el-pagination>
            </div>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.groupRemark"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.describer" type="textarea" :autosize="{ minRows: 2, maxRows: 4}"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.userName"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.userPass"></el-input>
          </el-form-item>
          <el-form-item label="ip" size="small">
            <el-input v-model="formUpdate.serverHost"></el-input>
          </el-form-item>
          <el-form-item label="port" size="small">
            <el-input v-model="formUpdate.serverPort"></el-input>
          </el-form-item>
          <el-form-item label="ip" size="small">
            <el-input v-model="formUpdate.devHost"></el-input>
          </el-form-item>
          <el-form-item label="port" size="small">
            <el-input v-model="formUpdate.devPort"></el-input>
          </el-form-item>
          <el-form-item label="ID" size="small">
            <el-input v-model="formUpdate.subnetId"></el-input>
          </el-form-item> 
          <el-form-item label="ID" size="small">
            <el-input v-model="formUpdate.devId"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="updateDialog = false"> </el-button>
        <el-button size="medium " @click="updateList('formUpdate')"> </el-button>
      </div>
    </el-dialog>
    </section>
</template>

<script>
import paging from "../paging"
import { getHardwareDevice,getProjectParam,addProjectParam,updateProjectParam,delProjectParam,xqSelectList,orgTree } from '../../url/api';
export default {
  props:['iotProjectparamtype'],
  data(){
    return{
      checkStrictly:false,
      bindRole:{},
      showmenuList:[],
      menuList:[],
      option1:[],
      xqTree:[],
      isAddorUpdate:1,//12
      formSearch:{
        current:1,
        size:10
      },
      formSearch1:{
        current:1,
        size:10
      },
      formData: [],//
      formData1: [],//
      formUpdate:{},//
      total: 0,//
      total1:0,//
      addDialog:false,
      iotProjectRadio:{},
      updateDialog:false,
      dialog:false,
      formPush:{},
      dataTree:[],
      defaultProps: {//key
        children: 'data',
        label: 'name'
      },
      defaultProps2: {
        children: 'children',
        label: 'name',
        value: 'id',
        expandTrigger: 'hover',checkStrictly: true
      },
      rules: {
        account:[{ required: true, message: ''}],
        orgId:[{ required: true, message: '',trigger: 'blur'}],
        xqId:[{ required: true, message: '',trigger: 'blur'}],
      },
      imageUrl:''
    }
  },
  methods:{
    goiotsmarthome1(){
      
      this.$emit("getProjectInfor",this.iotProjectRadio)
    },
    getHardwareDeviceList(){
      getHardwareDevice(this.formSearch1).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.formData1 = res.data.data.records.filter((item)=>{
            if(item.gmtCreate){
              item.gmtCreate = this.$root.getDateArray(item.gmtCreate)[9]
              // item.storeTime = this.$root.getDateArray(item.storeTime)[9]
            }
            return item
          })
          this.total1 = res.data.data.total
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    getlist(){
      getProjectParam(this.formSearch).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            if(item.gmtCreate){
              item.gmtCreate = this.$root.getDateArray(item.gmtCreate)[9]
              // item.storeTime = this.$root.getDateArray(item.storeTime)[9]
            }
            return item
          })
          this.total = res.data.data.total
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    getInit(){
      this.getlist()
      this.getHardwareDeviceList()
      xqSelectList({}).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.xqTree = res.data.data
        }
      })
      let org_tree={
        name:'',
        status:1
      }
      orgTree(org_tree).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.dataTree = res.data.data
        }
      })
    },
    handleChange(value){
      if(value.length!=0){
        console.log(value)
        this.formSearch.orgId=this.$refs.cascader.getCheckedNodes()[0].data.id
      }else{
        this.formSearch.orgId = ''
      }
    },
    handleChange1(value){
      if(value.length!=0){
        console.log(value)
        this.formPush.orgId=this.$refs.cascader.getCheckedNodes()[0].data.id
      }else{
        this.formPush.orgId = ''
      }
    },
    addList(addList){//node
      this.$refs[addList].validate((valid) => {
        if (valid) {
          addProjectParam(this.formPush).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.getInit()
              this.$message({
                message: '',
                type: 'success'
              });
              
            }else{
              this.$message(':'+res.data.msg)
            }
          })
          this.addDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    updateList(formName){//
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(this.formUpdate.hardwareDeviceName){
            delete this.formUpdate.hardwareDeviceName
            delete this.formUpdate.xqName
          }
          let params = this.formUpdate
          updateProjectParam(params).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.$message({
                message: '',
                type: 'warning'
              });
              this.getInit()
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
        this.updateDialog =false
      });
    },
    updateShowBox(item){//
      this.formUpdate = JSON.parse(JSON.stringify(item));
      this.updateDialog = true
      console.log(this.formUpdate)
    },
    deleInfor(id){
      let params = [id]
      this.$confirm('')
      .then(_ => {
        delProjectParam(params).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getlist()
          }else{
            this.$message(res.data.msg);
          }
        })
      })
      .catch(_ => {});

    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getlist()
    },
    handleCurrentPage1(val){//
      this.formSearch1.current=val
      this.getHardwareDeviceList()
    },
  },
  mounted(){
    this.getInit()
  },
  components:{
    paging
  }
}
</script>
<style scoped>
  .cont_box{
    width:100%;
    display: flex;
  }
  .cont_box_left,.cont_box_right{
    flex:1;
    background: transparent ;
    padding:10px;
  }
  .cont_box_left{
    margin-right:10px;
  }
  .margin10{
    margin-bottom:10px;
  }
  .marginRight{
    margin-bottom:10px;
    border-bottom: 1px solid #eee;
  }
  .cont_box_right .el-form-item{
    margin-bottom:20px;
  }
</style>



